<template><section><h1>ClassificationPrimitive</h1>
<p><code v-pre>vc-primitive-classification</code> 组件用于加载分类图元，通过它加载高亮的图形闭合体。支持加载 <code v-pre>BoxGeometry</code>、 <code v-pre>CylinderGeometry</code>、 <code v-pre>EllipsoidGeometry</code>、<code v-pre>PolylineVolumeGeometry</code>和 <code v-pre>SphereGeometry</code>；而 <code v-pre>CircleGeometry</code>、 <code v-pre>CorridorGeometry</code>、 <code v-pre>EllipseGeometry</code>、 <code v-pre>PolygonGeometry</code>和<code v-pre>RectangleGeometry</code> 需要拉伸才能正常渲染。</p>
<h2>示例</h2>
<h3>加载分类图元</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-provider-terrain-cesium></vc-provider-terrain-cesium>
        <vc-primitive-classification :asynchronous="false">
          <vc-instance-geometry :geometry.sync="geometry" :attributes="attributes">
            <vc-geometry-polygon :polygonHierarchy="polygonHierarchy" :extrudedHeight="extrudedHeight"></vc-geometry-polygon>
          </vc-instance-geometry>
        </vc-primitive-classification>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
<h4>代码</h4>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"viewer"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">vc-viewer</span> <span class="hljs-attr">@ready</span>=<span class="hljs-string">"ready"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-provider-terrain-cesium</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-provider-terrain-cesium</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-primitive-classification</span> <span class="hljs-attr">:asynchronous</span>=<span class="hljs-string">"false"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">vc-instance-geometry</span> <span class="hljs-attr">:geometry.sync</span>=<span class="hljs-string">"geometry"</span> <span class="hljs-attr">:attributes</span>=<span class="hljs-string">"attributes"</span>&gt;</span>
          <span class="hljs-tag">&lt;<span class="hljs-name">vc-geometry-polygon</span> <span class="hljs-attr">:polygonHierarchy</span>=<span class="hljs-string">"polygonHierarchy"</span> <span class="hljs-attr">:extrudedHeight</span>=<span class="hljs-string">"extrudedHeight"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-geometry-polygon</span>&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">vc-instance-geometry</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">vc-primitive-classification</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">vc-viewer</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">appearance</span>: <span class="hljs-literal">null</span>,
        <span class="hljs-attr">geometry</span>: {},
        <span class="hljs-attr">image</span>: <span class="hljs-string">'https://zouyaoji.top/vue-cesium/statics/SampleData/radarImage/1.png'</span>,
        <span class="hljs-attr">rectangle</span>: { <span class="hljs-attr">west</span>: <span class="hljs-number">102.4</span>, <span class="hljs-attr">south</span>: <span class="hljs-number">29.5</span>, <span class="hljs-attr">east</span>: <span class="hljs-number">106.5</span>, <span class="hljs-attr">north</span>: <span class="hljs-number">33.5</span> },
        <span class="hljs-attr">vertexFormat</span>: <span class="hljs-literal">null</span>,
        <span class="hljs-attr">attributes</span>: <span class="hljs-literal">null</span>,
        <span class="hljs-attr">extrudedHeight</span>: <span class="hljs-number">3000</span>,
        <span class="hljs-attr">polygonHierarchy</span>: [
          { <span class="hljs-attr">lng</span>: <span class="hljs-number">102.1</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">29.5</span> },
          { <span class="hljs-attr">lng</span>: <span class="hljs-number">106.2</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">29.5</span> },
          { <span class="hljs-attr">lng</span>: <span class="hljs-number">106.2</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">33.5</span> },
          { <span class="hljs-attr">lng</span>: <span class="hljs-number">102.1</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">33.5</span> }
        ]
      }
    },
    <span class="hljs-attr">methods</span>: {
      ready(cesiumInstance) {
        <span class="hljs-keyword">this</span>.cesiumInstance = cesiumInstance
        <span class="hljs-keyword">const</span> { Cesium, viewer } = <span class="hljs-keyword">this</span>.cesiumInstance
        viewer.scene.globe.depthTestAgainstTerrain = <span class="hljs-literal">true</span>
        viewer.camera.setView({
          <span class="hljs-attr">destination</span>: <span class="hljs-keyword">new</span> Cesium.Cartesian3(<span class="hljs-number">-1432246.8223880068</span>, <span class="hljs-number">5761224.588247942</span>, <span class="hljs-number">3297281.1889481535</span>),
          <span class="hljs-attr">orientation</span>: {
            <span class="hljs-attr">heading</span>: <span class="hljs-number">6.20312220367255</span>,
            <span class="hljs-attr">pitch</span>: <span class="hljs-number">-0.9937536846355606</span>,
            <span class="hljs-attr">roll</span>: <span class="hljs-number">0.002443376981836387</span>
          }
        })
        <span class="hljs-keyword">this</span>.attributes = {
          <span class="hljs-attr">color</span>: Cesium.ColorGeometryInstanceAttribute.fromColor(<span class="hljs-keyword">new</span> Cesium.Color.fromBytes(<span class="hljs-number">64</span>, <span class="hljs-number">157</span>, <span class="hljs-number">253</span>, <span class="hljs-number">100</span>))
        }
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
<h2>属性</h2>
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>geometryInstances</td>
<td>Object|Array</td>
<td></td>
<td><code v-pre>optional</code> 指定图元用于渲染的几何图形实例或图形实例集合。</td>
</tr>
<tr>
<td>appearance</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 指定图元的外观参数，如果具有 color 属性，默认为 PerInstanceColorAppearance 。</td>
</tr>
<tr>
<td>show</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> 指定图元是否显示。</td>
</tr>
<tr>
<td>vertexCacheOptimize</td>
<td>Boolean</td>
<td><code v-pre>false</code></td>
<td><code v-pre>optional</code> 指定是否优化几何体顶点着色器之前和之后的缓存。</td>
</tr>
<tr>
<td>interleave</td>
<td>Boolean</td>
<td><code v-pre>false</code></td>
<td><code v-pre>optional</code> 指定是否交错几何体顶点属性，true 时可以稍微改善渲染性能，但会增加加载时间。</td>
</tr>
<tr>
<td>compressVertices</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> 指定是否压缩几何体顶点，压缩可以以节省内存。</td>
</tr>
<tr>
<td>releaseGeometryInstances</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> 指定是否保留图元对几何体实例的输入，不保留可以节省内存。</td>
</tr>
<tr>
<td>allowPicking</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> 指定图元是否可以被 Scene.pick 拾取，关闭拾取可以节省内存。</td>
</tr>
<tr>
<td>asynchronous</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> 指定图元时异步加载还是同步加载。</td>
</tr>
<tr>
<td>classificationType</td>
<td>Number</td>
<td><code v-pre>2</code></td>
<td><code v-pre>optional</code> 指定是贴地形还是贴 3DTiles，还是两者都贴。 <strong>TERRAIN: 0, CESIUM_3D_TILE: 1, BOTH: 2</strong></td>
</tr>
<tr>
<td>debugShowBoundingVolume</td>
<td>Boolean</td>
<td><code v-pre>false</code></td>
<td><code v-pre>optional</code> 指定是否显示图元的边界球，用于调试使用。</td>
</tr>
<tr>
<td>debugShowShadowVolume</td>
<td>Boolean</td>
<td><code v-pre>false</code></td>
<td><code v-pre>optional</code> 指定是否绘制图元中每个几何图形的阴影体积，用于调试使用。</td>
</tr>
</tbody>
</table>
<hr>
<p>参考官方文档： <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/ClassificationPrimitive.html">ClassificationPrimitive</a></strong></p>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例，以及当前组件的 cesiumObject。</td>
</tr>
<tr>
<td>mousedown</td>
<td>{button,surfacePosition,target,type,windowPosition}</td>
<td>鼠标在该图元上按下时触发。</td>
</tr>
<tr>
<td>mouseup</td>
<td>{button,surfacePosition,target,type,windowPosition}</td>
<td>鼠标在该图元上弹起时触发。</td>
</tr>
<tr>
<td>click</td>
<td>{button,surfacePosition,target,type,windowPosition}</td>
<td>鼠标单击该图元时触发。</td>
</tr>
<tr>
<td>dblclick</td>
<td>{button,surfacePosition,target,type,windowPosition}</td>
<td>鼠标左键双击该图元时触发。</td>
</tr>
<tr>
<td>mousemove</td>
<td>{button,surfacePosition,target,type,windowPosition}</td>
<td>鼠标移动到该图元时触发。</td>
</tr>
</tbody>
</table>
<hr>
</section></template>

<script>
    export default {
      data() {
        return {
          appearance: null,
          geometry: {},
          image: 'https://zouyaoji.top/vue-cesium/statics/SampleData/radarImage/1.png',
          rectangle: { west: 102.4, south: 29.5, east: 106.5, north: 33.5 },
          vertexFormat: null,
          attributes: null,
          extrudedHeight: 3000,
          polygonHierarchy: [
            { lng: 102.1, lat: 29.5 },
            { lng: 106.2, lat: 29.5 },
            { lng: 106.2, lat: 33.5 },
            { lng: 102.1, lat: 33.5 }
          ]
        }
      },
      methods: {
        ready(cesiumInstance) {
          this.cesiumInstance = cesiumInstance
          const { Cesium, viewer } = this.cesiumInstance
          viewer.scene.globe.depthTestAgainstTerrain = true
          viewer.camera.setView({
            destination: new Cesium.Cartesian3(-1432246.8223880068, 5761224.588247942, 3297281.1889481535),
            orientation: {
              heading: 6.20312220367255,
              pitch: -0.9937536846355606,
              roll: 0.002443376981836387
            }
          })
          this.attributes = {
            color: Cesium.ColorGeometryInstanceAttribute.fromColor(new Cesium.Color.fromBytes(64, 157, 253, 100))
          }
        }
      }
    }
  </script>